button {
  width: 100px;
  height: 40px;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 600;
  display: block;
  box-shadow: inset 0 0 0 1px #000;
}

button[data-start] {
  background-color: green;
}

button[data-stop] {
  background-color: red;
}

.btn-wrapper {
  height: 90vh;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

